.show{
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
}

.appbar{
  background-color:white;
}

.showicons{
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: space-around;
}


.icons{
  border-radius: 50%;
  width: 90px;
  height: 90px;
  padding: 17px;
  background-color: white;
  border: 1px solid #000064;
  box-shadow: 0px 0px 0px 7px #4cc1bb;
}

.icons-parent{
  padding: 30px;

}

.icon{
  font-size: 50px;
  color: #20b2aa;
}


.icons-parent:hover .icons{
  background-color:#20b2aa;
  color:white;
  border: 2px solid white;
  box-shadow: 0px 0px 0px 7px #4cc1bb;
  transition: all .7s ease;
}
.icons-parent:hover .icon{
  color:white;
  transition: all .6s ease;
}


.icons2{
  width: 100px;
  height: 100px;
}


@media only screen and (min-width: 600px) {

.mainImage {
  display: flex;
  flex-flow: row wrap;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 600px;
  position: relative;
  -webkit-animation: mymove 15s infinite; /* Safari 4.0 - 8.0 */
  animation: mymove 15s infinite;
  background-size: 100% 100%;
  background-repeat: no-repeat;
}
#about-our-center-details{
  padding:30px 0 30px 70px;
}
#newsletter-submit{
  margin-left:-40px;
  margin-top:-4px;
}


#mainImage-text{
  padding-left: 10px;
  width:100%;
  margin-top:25%;
  font-family: sans-serif;
  font-size: 35px;
  font-weight: bold;
  text-shadow: 0 0 15px #20b2aa;
  color:white;
  z-index: 3;

}

}

@media only screen and (max-width: 599px) {
  #about-our-center-details{
    padding:30px 0 30px 5px;
  }
  
  .mainImage {
    display: flex;
    flex-flow: row wrap;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 300px;
    position: relative;
    -webkit-animation: mymove 20s infinite; /* Safari 4.0 - 8.0 */
    animation: mymove 20s infinite;
    background-size: 100% 100%;
    background-repeat: no-repeat;
  }
  
  #mainImage-text{
    z-index: 3;
    padding-left:10px;
    width:100%;
    margin-top: 30%;
    font-family: sans-serif;
    font-size: 20px;
    font-weight: bold;
    text-shadow: 2px 2px 15px #20b2aa;
    color:white;
  }
  
  #newsletter-submit{
    margin-left:0px;
    margin-top:0px;
  }
}



/* Standard syntax */
@keyframes mymove {
  0%{opacity: 0;}
  30%  {opacity: 1;   background: linear-gradient( rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4) ), url('jamkaran1.jpg'); background-position: center; }
  35%  {opacity: 0; }
  65%  {opacity: 1;    background: linear-gradient( rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4) ), url('jamkaran2.jpg');  background-position: center;  }
  70%  {opacity: 0; }
  100%  {opacity: 1;    background: linear-gradient( rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4) ), url('karbala1.jpg');  background-position: center;  }
}

.footericons{
  color:lightgray;
}

.title{
  padding: 10px;
}

.title-text {
  display:inline;
  text-align: center;
  position: relative;
  padding:10px;
  line-height: 100px;
  height: 100px;
  width: 300px;
}

.title-text:after {
  background: linear-gradient(to right, purple 25%,purple 25%, lightgreen 50%, lightgreen 50%, purple 75%, purple 75%);
  position: absolute;
  content: '';
  height: 4px;
  right: 0;
  left: 0;
  bottom: 0;
}


#img-loading {
  animation: imgload 2s infinite;
  box-shadow: 0px 0px 5px 10px #20b2aa;
  width: 300px;
  height:300px;
}



/* Standard syntax */
@keyframes imgload {
      from{opacity: 1;}
      to  {opacity: 0;}
}

